<template>
	<div class="addEdit-block">
		<el-form
			class="add-update-preview"
			ref="ruleForm"
			:model="ruleForm"
			:rules="rules"
			label-width="180px"
		>
			<template >
				<el-form-item class="input" v-if="type!='info'"  label="" prop="fuwumingcheng" >
					<el-input v-model="ruleForm.fuwumingcheng" placeholder="" clearable  :readonly="ro.fuwumingcheng"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="fuwumingcheng" >
					<el-input v-model="ruleForm.fuwumingcheng" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="select" v-if="type!='info'"  label="" prop="fuwuleixing" >
					<el-select :disabled="ro.fuwuleixing" v-model="ruleForm.fuwuleixing" placeholder="" >
						<el-option
							v-for="(item,index) in fuwuleixingOptions"
							v-bind:key="index"
							:label="item"
							:value="item">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="fuwuleixing" >
					<el-input v-model="ruleForm.fuwuleixing"
						placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="fuwushizhang" >
					<el-input v-model="ruleForm.fuwushizhang" placeholder="" clearable  :readonly="ro.fuwushizhang"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="fuwushizhang" >
					<el-input v-model="ruleForm.fuwushizhang" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="fuwujiage" >
					<el-input v-model.number="ruleForm.fuwujiage" placeholder="" clearable  :readonly="ro.fuwujiage"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="fuwujiage" >
					<el-input v-model="ruleForm.fuwujiage" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="fuwudianhua" >
					<el-input v-model="ruleForm.fuwudianhua" placeholder="" clearable  :readonly="ro.fuwudianhua"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="fuwudianhua" >
					<el-input v-model="ruleForm.fuwudianhua" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="upload" v-if="type!='info' && !ro.fuwutupian" label="" prop="fuwutupian" >
					<file-upload
						tip=""
						action="file/upload"
						:limit="3"
						:multiple="true"
						:fileUrls="ruleForm.fuwutupian?ruleForm.fuwutupian:''"
						@change="fuwutupianUploadChange"
					></file-upload>
				</el-form-item>
				<el-form-item class="upload" v-else-if="ruleForm.fuwutupian" label="" prop="fuwutupian" >
					<img v-if="ruleForm.fuwutupian.substring(0,4)=='http'&&ruleForm.fuwutupian.split(',w').length>1" class="upload-img" style="margin-right:20px;" v-bind:key="index" :src="ruleForm.fuwutupian" width="100" height="100">
					<img v-else-if="ruleForm.fuwutupian.substring(0,4)=='http'" class="upload-img" style="margin-right:20px;" v-bind:key="index" :src="ruleForm.fuwutupian.split(',')[0]" width="100" height="100">
					<img v-else class="upload-img" style="margin-right:20px;" v-bind:key="index" v-for="(item,index) in ruleForm.fuwutupian.split(',')" :src="$base.url+item" width="100" height="100">
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="dianpumingcheng" >
					<el-input v-model="ruleForm.dianpumingcheng" placeholder="" clearable  :readonly="ro.dianpumingcheng"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="dianpumingcheng" >
					<el-input v-model="ruleForm.dianpumingcheng" placeholder="" readonly></el-input>
				</el-form-item>
			</template>
			<el-form-item v-if="type!='info'"  label="" prop="fuwujieshao" >
				<editor 
					style="min-width: 200px; max-width: 600px;"
					v-model="ruleForm.fuwujieshao" 
					class="editor" 
					action="file/upload">
				</editor>
			</el-form-item>
			<el-form-item v-else-if="ruleForm.fuwujieshao" label="" prop="fuwujieshao" >
				<span class="text ql-snow ql-editor" v-html="ruleForm.fuwujieshao"></span>
			</el-form-item>
			<el-form-item class="btn">
				<el-button class="btn3"  v-if="type!='info'" type="success" @click="onSubmit">
					<span class="icon iconfont icon-xihuan"></span>
					
				</el-button>
				<el-button class="btn4" v-if="type!='info'" type="success" @click="back()">
					<span class="icon iconfont icon-xihuan"></span>
					
				</el-button>
				<el-button class="btn5" v-if="type=='info'" type="success" @click="back()">
					<span class="icon iconfont icon-xihuan"></span>
					
				</el-button>
			</el-form-item>
		</el-form>
    

	</div>
</template>
<script>
	import { 
		isIntNumer,
		isMobile,
	} from "@/utils/validate";
	export default {
		data() {
			var validateMobile = (rule, value, callback) => {
				if(!value){
					callback();
				} else if (!isMobile(value)) {
					callback(new Error(""));
				} else {
					callback();
				}
			};
			var validateIntNumber = (rule, value, callback) => {
				if(!value){
					callback();
				} else if (!isIntNumer(value)) {
					callback(new Error(""));
				} else {
					callback();
				}
			};
			return {
				id: '',
				type: '',
			
			
				ro:{
					fuwumingcheng : false,
					fuwuleixing : false,
					fuwushizhang : false,
					fuwujiage : false,
					fuwudianhua : false,
					fuwujieshao : false,
					fuwutupian : false,
					dianpumingcheng : false,
					clicktime : false,
					clicknum : false,
					discussnum : false,
					storeupnum : false,
				},
			
				ruleForm: {
					fuwumingcheng: '',
					fuwuleixing: '',
					fuwushizhang: '',
					fuwujiage: '',
					fuwudianhua: '',
					fuwujieshao: '',
					fuwutupian: '',
					dianpumingcheng: '',
					clicktime: '',
				},
				fuwuleixingOptions: [],

				rules: {
					fuwumingcheng: [
					],
					fuwuleixing: [
					],
					fuwushizhang: [
					],
					fuwujiage: [
						{ validator: validateIntNumber, trigger: 'blur' },
					],
					fuwudianhua: [
						{ validator: validateMobile, trigger: 'blur' },
					],
					fuwujieshao: [
					],
					fuwutupian: [
					],
					dianpumingcheng: [
					],
					clicktime: [
					],
					clicknum: [
						{ validator: validateIntNumber, trigger: 'blur' },
					],
					discussnum: [
						{ validator: validateIntNumber, trigger: 'blur' },
					],
					storeupnum: [
						{ validator: validateIntNumber, trigger: 'blur' },
					],
				},
			};
		},
		props: ["parent"],
		computed: {



		},
		components: {
		},
		created() {
		},
		methods: {
			// 
			download(file){
				window.open(`${file}`)
			},
			// 
			init(id,type) {
				if (id) {
					this.id = id;
					this.type = type;
				}
				if(this.type=='info'||this.type=='else'||this.type=='msg'){
					this.info(id);
				}else if(this.type=='logistics'){
					for(let x in this.ro) {
						this.ro[x] = true
					}
					this.logistics=false;
					this.info(id);
				}else if(this.type=='cross'){
					var obj = this.$storage.getObj('crossObj');
					for (var o in obj){
						if(o=='fuwumingcheng'){
							this.ruleForm.fuwumingcheng = obj[o];
							this.ro.fuwumingcheng = true;
							continue;
						}
						if(o=='fuwuleixing'){
							this.ruleForm.fuwuleixing = obj[o];
							this.ro.fuwuleixing = true;
							continue;
						}
						if(o=='fuwushizhang'){
							this.ruleForm.fuwushizhang = obj[o];
							this.ro.fuwushizhang = true;
							continue;
						}
						if(o=='fuwujiage'){
							this.ruleForm.fuwujiage = obj[o];
							this.ro.fuwujiage = true;
							continue;
						}
						if(o=='fuwudianhua'){
							this.ruleForm.fuwudianhua = obj[o];
							this.ro.fuwudianhua = true;
							continue;
						}
						if(o=='fuwujieshao'){
							this.ruleForm.fuwujieshao = obj[o];
							this.ro.fuwujieshao = true;
							continue;
						}
						if(o=='fuwutupian'){
							this.ruleForm.fuwutupian = obj[o];
							this.ro.fuwutupian = true;
							continue;
						}
						if(o=='dianpumingcheng'){
							this.ruleForm.dianpumingcheng = obj[o];
							this.ro.dianpumingcheng = true;
							continue;
						}
						if(o=='clicktime'){
							this.ruleForm.clicktime = obj[o];
							this.ro.clicktime = true;
							continue;
						}
						if(o=='clicknum'){
							this.ruleForm.clicknum = obj[o];
							this.ro.clicknum = true;
							continue;
						}
						if(o=='discussnum'){
							this.ruleForm.discussnum = obj[o];
							this.ro.discussnum = true;
							continue;
						}
						if(o=='storeupnum'){
							this.ruleForm.storeupnum = obj[o];
							this.ro.storeupnum = true;
							continue;
						}
					}
				}
				// 
				this.$http({
					url: `${this.$storage.get('sessionTable')}/session`,
					method: "get"
				}).then(({ data }) => {
					if (data && data.code === 0) {
						var json = data.data;
						if(((json.dianpumingcheng!=''&&json.dianpumingcheng) || json.dianpumingcheng==0) && this.$storage.get("role")!=""){
							this.ruleForm.dianpumingcheng = json.dianpumingcheng
							this.ro.dianpumingcheng = true;
						}
					} else {
						this.$message.error(data.msg);
					}
				});
				this.$http({
					url: `option/fuwuleixing/fuwuleixing`,
					method: "get"
				}).then(({ data }) => {
					if (data && data.code === 0) {
						this.fuwuleixingOptions = data.data;
					} else {
						this.$message.error(data.msg);
					}
				});
			
			},
			// 

			info(id) {
				this.$http({
					url: `jiazhengfuwu/info/${id}`,
					method: "get"
				}).then(({ data }) => {
					if (data && data.code === 0) {
						this.ruleForm = data.data;
						//
						let reg=new RegExp('../../../upload','g')//g
						this.ruleForm.fuwujieshao = this.ruleForm.fuwujieshao.replace(reg,'../../../springbootmwrnnvi8/upload');
					} else {
						this.$message.error(data.msg);
					}
				});
			},

			// 
			async onSubmit() {
					if(this.ruleForm.fuwutupian!=null) {
						this.ruleForm.fuwutupian = this.ruleForm.fuwutupian.replace(new RegExp(this.$base.url,"g"),"");
					}
					var objcross = this.$storage.getObj('crossObj');
					await this.$refs["ruleForm"].validate(async valid => {
						if (valid) {
							if(this.type=='cross'){
								var statusColumnName = this.$storage.get('statusColumnName');
								var statusColumnValue = this.$storage.get('statusColumnValue');
								if(statusColumnName!='') {
									var obj = this.$storage.getObj('crossObj');
									if(statusColumnName && !statusColumnName.startsWith("[")) {
										for (var o in obj){
											if(o==statusColumnName){
												obj[o] = statusColumnValue;
											}
										}
										var table = this.$storage.get('crossTable');
										await this.$http({
											url: `${table}/update`,
											method: "post",
											data: obj
										}).then(({ data }) => {});
									}
								}
							}
							
							await this.$http({
								url: `jiazhengfuwu/${!this.ruleForm.id ? "save" : "update"}`,
								method: "post",
								data: this.ruleForm
							}).then(async ({ data }) => {
								if (data && data.code === 0) {
									this.$message({
										message: "",
										type: "success",
										duration: 1500,
										onClose: () => {
											this.parent.showFlag = true;
											this.parent.addOrUpdateFlag = false;
											this.parent.jiazhengfuwuCrossAddOrUpdateFlag = false;
											this.parent.search();
											this.parent.contentStyleChange();
										}
									});
								} else {
									this.$message.error(data.msg);
								}
							});
						}
					});
			},
			// uuid
			getUUID () {
				return new Date().getTime();
			},
			// 
			back() {
				this.parent.showFlag = true;
				this.parent.addOrUpdateFlag = false;
				this.parent.jiazhengfuwuCrossAddOrUpdateFlag = false;
				this.parent.contentStyleChange();
			},
			fuwutupianUploadChange(fileUrls) {
				this.ruleForm.fuwutupian = fileUrls;
			},
		}
	};
</script>
